import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';

import { StormpathModule, StormpathConfiguration } from 'angular-stormpath';
import { AppComponent } from './app.component';
import { PeopleComponent } from './people/people.component';
import {Routes,RouterModule} from '@angular/router';
import { HomeComponent } from './home/home.component';
import { WorkComponent } from './work/work.component';
import { ContactComponent } from './contact/contact.component';
import { StudentsComponent } from './students/students.component';
import { FacultyComponent } from './faculty/faculty.component';

const appRoutes: Routes=[{path:'', component:HomeComponent },{path:'home', component:HomeComponent },{path:'faculty', component:FacultyComponent },{path:'students', component:StudentsComponent },{path:'people', component:PeopleComponent },{path:'work', component:WorkComponent },{path:'contact', component:ContactComponent }]

export function stormpathConfig(): StormpathConfiguration {
  let spConfig: StormpathConfiguration = new StormpathConfiguration();
  spConfig.endpointPrefix = 'https://{DNS-LABEL}.apps.stormpath.io';
  return spConfig;
 }

@NgModule({
  declarations: [
    AppComponent,
    PeopleComponent,HomeComponent, WorkComponent, ContactComponent, StudentsComponent, FacultyComponent
    
  ],
  imports: [RouterModule.forRoot(appRoutes),
    BrowserModule,
    HttpClientModule,
    
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



